* {
  -webkit-tap-highlight-color: transparent;
}

.tilesWrap {
  margin: 50px auto;
  list-style: none;
  text-align: center;
}

.tilesWrap li {
  display: inline-block;
  width: 80%;
  min-width: 50px;
  max-width: 500px;
  padding: 80px 20px 40px;
  position: relative;
  vertical-align: top;
  margin: 30px;
  font-family: "helvetica", san-serif;
  font-weight: bold;
  font-size: large;
  min-height: 25vh;
  background: #262a2b;
  border: 1px solid #252727;
  text-align: left;
}

.tilesWrap li h2 {
  font-size: 114px;
  margin: 0;
  position: absolute;
  opacity: 0.2;
  top: 50px;
  right: 10px;
  transition: all 0.3s ease-in-out;
}

.tilesWrap li h3 {
  font-size: 20px;
  color: #b7b7b7;
  margin-bottom: 5px;
}

.tilesWrap li p {
  font-size: 16px;
  line-height: 18px;
  color: #b7b7b7;
  margin-top: 5px;
}

.tilesWrap li button {
  background: transparent;
  border: 1px solid #b7b7b7;
  padding: 10px 20px;
  color: #b7b7b7;
  border-radius: 3px;
  position: relative;
  transition: all 0.3s ease-in-out;
  transform: translateY(-40px);
  opacity: 0;
  cursor: pointer;
  /* overflow: hidden; */
}

.tilesWrap li button:before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background: #b7b7b7;
  top: 0;
  opacity: 0;
  left: -140px;
  border-radius: 0 20px 20px 0;
  z-index: -1;
  transition: all 0.3s ease-in-out;
}

.tilesWrap li:hover button {
  transform: translateY(5px);
  opacity: 1;
}

.tilesWrap li button:hover {
  color: #262a2b;
}

.tilesWrap li button:hover:before {
  left: 0;
  opacity: 1;
}

.tilesWrap li:hover h2 {
  top: 0px;
  opacity: 0.6;
}

.tilesWrap li:before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  z-index: -1;
  background: #fff;
  transform: skew(2deg, 2deg);
}

.tilesWrap li:after {
  content: "";
  position: absolute;
  width: 40%;
  height: 100%;
  right: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.02);
}

.tilesWrap li:nth-child(odd):before {
  background: #c9ffbf;
  background: linear-gradient(to right, #ffafbd, #c9ffbf);
}

.tilesWrap li:nth-child(even):before {
  background: #f2709c;
  background: linear-gradient(to right, #ff9472, #f2709c);
}

.header {
  padding: -13px;
  box-shadow: 0 5px 15px rgba(184, 99, 233, 0.26);
  z-index: 50;
  position: relative;
  top: 0;
  left: 0;
}

h1 {
  text-align: center;
  color: hsl(0, 0%, 28%);
  font-size: 50px;
  font-weight: 800;
  font-family: monospace;
  letter-spacing: 7px;
  cursor: pointer;
  text-transform: uppercase;
  padding: 20px;
  background: linear-gradient(
    to right,
    hsl(0, 0%, 30%) 0,
    #883ff9 10%,
    hsl(0, 0%, 30%) 20%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: shine 3s infinite linear;
}

@keyframes shine {
  0% {
    background-position: 0;
  }
  60% {
    background-position: 600px;
  }
  100% {
    background-position: 600px;
  }
}

body {
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: space-between;
  flex-direction: column;
  background: rgba(0, 0, 0, 0.83)
    url("https://media.giphy.com/media/doXBzUFJRxpaUbuaqz/giphy.gif");
  background-size: cover;
  background-position: center;
  background-blend-mode: darken;
  font-family: sans-serif;
  -webkit-tap-highlight-color: transparent;
}

.container {
  width: 100%;
  max-width: 650px;
  background: #140615b3;
  padding: 28px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 2px rgba(6, 0, 8, 0.66);
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: 40px;
}

.container__form-title {
  font-size: 26px;
  font-weight: 600;
  opacity: 100%;
  text-align: center;
  padding-bottom: 6px;
  color: white;
  text-shadow: 2px 2px 2px black;
  border-bottom: solid 1px white;
}

.container__main-user-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px 0;
}

.container__user-input-box {
  display: flex;
  flex-wrap: wrap;
  width: 50%;
  padding-top: 15px;
}

.container__user-input-box label {
  width: 95%;
  color: white;
  font-size: 20px;
  font-weight: 400;
  margin: 5px 0;
}

.container__user-input-box input {
  height: 40px;
  width: 95%;
  border-radius: 7px;
  outline: none;
  border: 1px solid grey;
  padding: 0 10px;
}

.container__gender-title {
  color: white;
  font-size: 24px;
  font-weight: 600;
  border-bottom: 1px solid white;
}

.container__gender-category {
  margin: 15px 0;
  color: white;
}

.container__gender-category label {
  padding: 0 20px 0 5px;
}

.container__gender-category label,
.container__gender-category input,
.container__btn {
  cursor: pointer;
}

.container__btn {
  display: block;
  width: 100%;
  margin-top: 10px;
  font-size: 20px;
  padding: 10px;
  border: none;
  border-radius: 10px;
  color: rgb(209, 209, 209);
  background: #36004d;
}

.container__btn:hover {
  background: #030003b3;
  color: rgba(252, 249, 249, 0.7);
}

@media (max-width: 600px) {
  .container {
    min-width: 280px;
  }
  .container__user-input-box {
    margin-bottom: 12px;
    width: 100%;
  }

  .container__user-input-box:nth-child(2n) {
    justify-content: space-between;
  }

  .container__gender-category {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .container__main-user-info {
    max-height: 380px;
    /* overflow: auto; */
  }
  .container__main-user-info::-webkit-scrollbar {
    width: 0;
  }
}
